.guild_container {
  color: #ffffff;
  padding-top: 25px;
  padding-left: 50px;
}

.guild_menu {
  display: flex;
  margin-bottom: 20px;
  border-bottom: 0.5px solid #92acf2;
  width: 60%;
  min-width: 700px;
  height: 30px;
}

.guild_title {
  margin-left: 5vw;
}

.guild_menu {
  margin-left: 5vw;
}

.guild_menu_component {
  margin-right: 8%;
  cursor: pointer;
}

.guild_menu_content {
  padding-bottom: 20px;
}

.guild-home-container {
  display: flex;
  flex-direction: row;
  margin-left: 8vw;
  padding-top: 20px;
}

.move_to_ctf {
  border: 0.5px solid #8098bc;
  width: 23%;
  height: 200px;
  padding: 30px;
  margin-right: 20px;
  border-radius: 10px;
  min-width: 250px;
}

.move_to_ctf_txt {
  font-size: x-large;
  padding-bottom: 80px;
}

.move_to_ctf_btn {
  background-color: #3c6ef0;
  color: #ffffff;
  width: 60%;
  height: 50px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-size: medium;
  font-weight: 600;
}

.move_to_wargame {
  min-width: 250px;
  border: 0.5px solid #8098bc;
  width: 23%;
  padding: 30px;
  border-radius: 10px;
}

.move_to_wargame_txt {
  font-size: x-large;
  padding-bottom: 80px;
}

.move_to_wargame_btn {
  background-color: #92acf2;
  color: #093ab9;
  width: 70%;
  height: 50px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-size: medium;
  font-weight: 600;
}

.guild_component_container {
  display: flex;
}

.personal_guild_info_container {
  margin-left: 60px;
  padding: 20px;
  width: 20%;
  min-width: 300px;
  background-color: #242424;
  border-radius: 10px;
  height: 450px;
}

.personal_guild_detail_container {
  padding: 20px;
  background-color: #1c1c1c;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
}

.personal_guild_detail {
  display: flex;
}

.personal_guild_txt {
  margin-left: 20px;
}

.personal_guild_btn {
  align-self: center;
  margin-top: 20px;
  width: 80%;
  background-color: #3c6ef0;
  border: 1px solid #8098bc;
  color: #ffffff;
  border-radius: 5px;
  height: 40px;
  cursor: pointer;
}

.personal_detail_my_info_title {
  border-top: 1px solid #92acf2;
  padding-top: 20px;
}

.personal_detail_my_info {
  width: auto;
}

.personal_detail_my_info {
  background-color: #1c1c1c;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  align-items: center;
}

.personal_detail_profile_sample {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.personal_detail_profile_txt {
  margin-left: 20px;
}

.personal_detail_profile_group_txt {
  font-size: small;
  margin-top: 10px;
}

.personal_detail_name_id_container {
  display: flex;
}

.personal_info_id_copy_button {
  background-color: transparent;
  border: none;
  color: #878887;
  cursor: pointer;
  margin-left: 20px;
}

.personal_detail_guild_admin {
  margin-bottom: 5px;
  color: #093ab9;
  background-color: #92acf2;
  font-size: small;
  width: 30%;
  border-radius: 10px;
  padding: 2px;
}

.my_guild_invite_btn {
  background-color: transparent;
  cursor: pointer;
  border: 1px solid #818181;
  color: #818181;
  border-radius: 10px;
  margin-left: auto;
}

.login-prompt-container {
  padding: 50px;
  margin-left: 50px;
  color: white;
}

.login-prompt-btn {
  margin-bottom: 5px;
  color: #093ab9;
  background-color: #92acf2;
  font-size: small;
  width: 200px;
  height: 50px;
  border-radius: 10px;
  padding: 2px;
  cursor: pointer;
  margin-top: 10px;
  border: none;
}

.login-prompt-btn:hover {
  background-color: #3c6ef0; /* Change to desired hover color */
  color: #ffffff; /* Change text color on hover */
}
